.container{
    max-width: 1050px;
    margin: 20px auto 0 auto;
}
.tabPanelBg{
    background: #FAFBFE;
}
.mainTabs{
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
}
.mainTabs .tab{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    color: #232427;
    padding: 10px 5px;
    margin: 0 60px;
    cursor: pointer;
}
.mainTabs .tab.react-tabs__tab--selected{
    border-bottom: 2px solid #232427;
    font-weight: 700;
}
.mainTabs .tab:focus-visible {
    outline: none;
}
.filterPart{
    padding-top: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.heading{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    color: #232427;
}
.swithPart,
.buttonsPart{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 28px;
}
.buttonsPart{
    flex-wrap: wrap;
    padding: 0 70px;
}
.buttonsPart button{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #000000;
    background: transparent;
    border: 1.5px solid #0F1420;
    border-radius: 40px;
    padding: 2px 10px;
    cursor: pointer;
    margin: 0 10px 15px 10px;
}
.switch{
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
    margin: 0 32px;
}
.switchText{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: #808285;
}
.switchText.active{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: #232427;
}
.searchPart{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-top: 48px;
}
.searchField{
    position: relative;
    width: 80%;
}
.searchField .searchIcon{
    position: absolute;
    top: 15px;
    left: 25px;
}
.searchField input{
    background: #F0F2F8;
    border-radius: 30px;
    border: none;
    height: 48px;
    padding: 0 22px 0 62px;
    font-size: 18px;
    color: #000000;
    width: 90%;
}
.searchField input::placeholder{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 28px;
    color: #6E757C;
}
.searchField input:focus-visible {
    outline: none;
}
.selectSort label,
.selectSort select{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    color: #0F1420;
    background: transparent;
}
.selectSort select{
    margin-left: 10px;
    border: none;
    outline: none;
    padding: 0 20px 0 0;
    background: transparent;
}
.filterFields{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-top: 24px;
}
.filterLeft{
    display: flex;
    align-items: center;
}
.selectFilter{
    position: relative;
}
.selectFilter select{
    border: 1.5px solid #000000;
    border-radius: 30px;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    color: #232427;
    height: 42px;
    margin-right: 30px;
    padding: 0 5px 0 35px;
    background: transparent;
}
.selectFilter span{
    position: absolute;
    top: 12px;
    left: 15px;
}
.filterRight button{
    border: 1.5px solid #808285;
    border-radius: 40px;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    color: #808285;
    height: 42px;
    padding: 0 20px;
    background: transparent;
    cursor: pointer;
}
.totalResults{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    color: #0F1420;
    margin: 32px 0;
}
.jobSection{
    padding-bottom: 60px;
}
.jobItem{
    background: #FFFFFF;
    box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.08);
    border-radius: 5px;
    padding: 34px 24px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 20px;
    position: relative;
}
.jobSection.applied .jobItem{
    padding: 70px 24px 34px 24px;
}
.decision span{
    margin-right: 10px;
}
.jobItem .itemImage{
    width: 20%;
    cursor: pointer;
}
.jobItem .itemDetails{
    width: 70%;
}
.jobItem .sharePart{
    text-align: right;
    width: 20%;
}
.jobItem .jobTags button{
    border-radius: 40px;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 590;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    border: none;
    outline: none;
    height: 30px;
    padding: 0 15px;
    margin-right: 12px;
    cursor: pointer;
}
.jobItem .jobTags button.default{
    font-style: normal;
    font-weight: 590;
    font-size: 14px;
    line-height: 17px;
    color: #0F1420;
    border: 1.5px solid #0F1420;
    border-radius: 40px;
    background: transparent;
}
.decision{
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 800;
    font-size: 20px;
    line-height: 23px;
    color: #384D98;
    position: absolute;
    top: 15px;
    display: flex;
    align-items: center;
}
.lightGreen{
    background: #E7FFCD;
}
.lightPink{
    background: #FCCDED;;
}
.lightBlue{
    background: #CCE6FF;
}
.lightRed{
    background: #FFE6CD;
}
.darkBlack{
    background: #0F1420;
    color: white !important;
}
.jobPosition{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 22px;
    line-height: 26px;
    color: #0F1420;
    margin-top: 9px;
    cursor: pointer;
}
.jobdetail{
    margin-top: 8px;
}
.jobdetail .jobCompany{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 590;
    font-size: 16px;
    line-height: 19px;
    color: #0F1420;
    border-right: 1px solid #0F1420;
    padding-right: 15px;
    cursor: pointer;
}
.jobRate{
    border-left: 1px solid #0F1420;
    padding-left: 15px;
}
.jobMap{
    padding: 0 15px;
}
.jobMap .location{
    padding-left: 5px;
}
.jobMap .location,
.jobRate{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #0F1420; 
}
.jobSkill{
    margin-top: 21px;
}
.jobSkill .skillText{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 590;
    font-size: 16px;
    line-height: 19px;
    color: #0F1420;
}
.skillsList{
    margin-top: 12px;
}
.skillsList button{
    background: #0F1420;
    border-radius: 40px;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 590;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
    height: 30px;
    padding: 0 15px;
    border: none;
    outline: none;
    margin-right: 10px;
    cursor: pointer;
}
.shareIcons span{
    cursor: pointer;
    margin-left: 24px;
}
.jobPosted{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #333A45;
    margin-top: 42px;
}
.jobApply{
    margin-top: 35px;
    display: flex;
    justify-content: flex-end;
}
.jobApply a{
    background: linear-gradient(86.54deg, #F26A5C 7.4%, #F29C6B 115.84%);
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 50px;
    height: 40px;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 23px;
    color: #FFFFFF;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    width: auto;
    padding: 0 35px;
}
.filterPart.applied{
    padding-top: 6px;
}
.filterPart.applied .searchPart{
    margin-bottom: 32px;
}
.mainTabs{
    padding: 0;
}
@media (width:1024px){
    .container {
        max-width: 1000px;
    }
}
@media screen and (max-width:768px){
    .headerRight{
        display: none;
    }
    .mainTabs .tab {
        margin: 0 10px;
    }
    .selectFilter select {
        margin-right: 5px;
        padding: 0 5px 0 25px;
        font-size: 14px;
    }
    .searchPart {
        margin-top: 20px;
        display: block;
    }
    .switch {
        margin: 0 15px;
    }
    .switchText,.switchText.active{
        font-size: 16px;
        line-height: 20px
    }
    .selectSort{
        margin-top: 10px;
        text-align: center;
    }
    .container {
        max-width: 100%;
        margin: 20px 10px 0 10px;
    }
    .jobItem{
        display: block;
    }
    .jobItem .itemImage,
    .jobItem .itemDetails,
    .jobItem .sharePart,
    .detail .jobItem .itemDetails,
    .jobApply a{
        width: 100%;
    }
    .buttonsPart {
        padding: 0px 10px;
    }
    .shareIcons,.jobApply {
        justify-content: flex-start;
    }
    .searchField input{
        width:95%
    }
    .jobPosted {
        margin-top: 10px;
    }
    .filterPart {
        padding-top: 15px;
    }
    .jobTags{
        display: inline-block;
    }
    .jobItem .jobTags button {
        padding: 0 10px;
        margin-right: 5px;
        margin-top: 10px;
    }
    .jobdetail{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .jobdetail .jobCompany{
        border-right: none;
    }
    .jobRate,.jobMap{
        border-left: none;
        padding: 0;
    }
    .jobMap{
        margin: 10px 0;
    }
    .detail .jobItem .itemImage img,
    .itemImage img{
        height: 84px;
    }
    .jobItem .sharePart {
        text-align: left;
        margin-top: 15px;
    }
    .shareIcons span {
        margin-left: 0;
    }
    .detail .jobApply,
    .jobApply {
        margin-top: 20px;
    }
    .detail .tDetail{
        display: block;
    }
    .detail .bDetail{
        display: block;
    }
    .jobStatus{
        font-size: 18px;
        padding: 5px;
        margin-top: 15px;
    }
    .detail .jobItem .sharePart{
        width: 100%;
    }
    .gridSection{
        display: block;
    }
    .gridItem{
        padding: 15px 20px;
    }
    .gridMainSection{
        display: block;
    }
    .mainLeft{
        padding: 20px;
        margin-bottom: 10px;
    }
    .bottomButtons{
        display: block;
    }
    .bottomButtons button{
        display: block;
        margin-bottom: 10px;
        width: 100%;
    }
    .detail .container{
        padding-top: 15px;
    }
}

