.detail .container{
    margin-top: 0;
    padding-top: 32px;
}
.detail .jobItem .sharePart{
    width: 35%;
}
.detail .jobItem .itemImage {
    width: 16%;
}
.detail .jobItem .itemImage img{
    height: 130px;
}
.detail .jobdetail .jobCompany{
    border-right: none;
    font-weight: 400;
    margin-top: 20px;
}
.detail .jobItem{
    flex-direction: column;
    box-shadow: none;
}
.detail .tDetail,
.detail .bDetail{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
}
.detail .jobApply {
    margin-top: 0;
}
.detail .jobSection {
    padding-bottom: 24px;
}
.bDetail .jobTags{
    margin-top: 10px;
}
.bDetail{
    margin-top: 10px;
}
.jobStatus {
    background: #BBFEB5;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 590;
    font-size: 20px;
    line-height: 24px;
    color: #000000;
    border-radius: 3px;
    padding: 6px 10px;
    margin-top: 31px;
    text-align: center;

}
.backBtn{
    display: flex;
    align-items: center;
    text-decoration: none;
    margin-bottom: 10px;
}
.backBtn .backText{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 23px;
    color: #232427;
    margin-left: 8px;
}
.gridSection{
    display: grid;
    grid-template-columns: 22% 22% 22% 22%;
    column-gap: 4%;
}
.gridItem{
    background: #FFFFFF;
    border-radius: 5px;
    padding: 15px 24px;
}
.gridItem .gridTop{
    display: flex;
    align-items: center;
}
.gridTop .gridIcon{
    margin-right: 21px;
}
.gridTop .gridHead{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 590;
    font-size: 18px;
    line-height: 21px;
    color: #000000;
}
.gridButtons{
    display: flex;
    flex-wrap: wrap;
    margin-top: 18px;
    justify-content: center;
}
.gridButtons .gBtn{
    border-radius: 40px;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 590;
    font-size: 14px;
    line-height: 17px;
    background: #0F1420;
    color: #fff;
    border: none;
    outline: none;
    height: 30px;
    padding: 0 10px;
    margin-right: 12px;
    margin-bottom: 12px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}
.gBtn .icon{
    margin-right: 6px;
}
.gridDetail{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 28px;
    color: #000000;
    margin-left: 40px;
}
.gridMainSection{
    display: grid;
    grid-template-columns: 74% 1fr;
    column-gap: 4%;
    margin-top: 4%;
}

.mainLeft, .mainRight{
    background: #fff;
    padding: 18px 30px;
}
.mainLeft{
    padding: 33px 71px;
}
.heading{
    font-style: normal;
    font-weight: 590;
    font-size: 18px;
    line-height: 21px;
    color: #000000;
}
.paragraph{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: #000000;
}
.bottomButtons{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 25px;
    margin-bottom: 30px;
}
.bottomButtons .applyBtn{
    background: linear-gradient(86.54deg, #F26A5C 7.4%, #F29C6B 115.84%);
    border-radius: 50px;
    outline: none;
    border: none;
    height: 44px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 23px;
    color: #FFFFFF;
    padding: 0 30px;
}
.bottomButtons .blackBtn{
    height: 44px;
    background: #0F1420;
    border-radius: 50px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 30px;
}
.bottomButtons .blackBtn .icon{
    margin-right: 15px;
}
.bottomButtons .blackBtn .text{
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 23px;
    color: #FFFFFF;
}
.locationBtn{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 15px;
    margin-left: 40px;
}
.locationBtn .lightPink{
    border-radius: 40px;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 590;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    border: none;
    outline: none;
    height: 30px;
    padding: 0 15px;
    margin-right: 12px;
    cursor: pointer;
}
.mainRight .head{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 590;
    font-size: 18px;
    line-height: 21px;
    color: #000000;
}
.posterMain{
    display: flex;
    align-items: center;
    margin: 25px 0;
}
.posterDetail{
    margin-left: 10px;
}
.posterDetail p{
    margin: 0;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: #000000;
}
.posterDetail label{
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    color: #000000;
}
.posterBtn{
    display: flex;
    justify-content: center;
    align-items: center;
}
.posterBtn button{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 34px;
    background: #0F1420;
    border-radius: 50px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    color: #FFFFFF;
    padding: 0 20px;
}
.detail .jobItem .itemDetails {
    width: 60%;
}
.backBtn .backIcon{
    display: flex;
}
.posterBtn button .icon{
    margin-right: 10px;
    display: flex;
}
.shareIcons{
    justify-content: flex-end;
    display: flex;
    align-items: center;
}