.header{
    height: 60px;
    background: #FFFFFF;
    box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.08);
    padding: 0 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.headerLeft{
    display: flex;
    text-decoration: none;
}
.logo{
    font-family: 'Lora', serif;
    font-style: normal;
    font-weight: 600;
    font-size: 32px;
    line-height: 41px;
    color: #14203A;
    margin-left: 16px;
}
.headerRight{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 380px;
}
.headerRight .navLink{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
}
.headerRight .navLink .navText{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 11px;
    line-height: 13px;
    color: #000000;
}
